body {
  background-color: black;
}

#space {
  position: absolute;
  width: 100%;
  top: 0;
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  gap: 8px;
  --box-size: 40px;
}

.boxContainer {
  width: var(--box-size);
  position: relative;
}

.box {
  width: var(--box-size);
  height: var(--box-size);
  background: #eee;
  transform-origin: center bottom;
  border-radius: 50%;
  cursor: pointer;
}

.dust {
  position: absolute;
  bottom: -25px !important;
  left: 0;
  right: 0;
  height: 2px;
  bottom: 0px;
  background-color: #ccc;
}
